.header{
    padding: 0 0 41px;
    border-bottom: 1px solid rgba(84, 84, 84, 0.7);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;

}

@media screen and (max-width: 970px) {
    .header {
        padding: 0 19px 41px;
    }
}

@media screen and (max-width: 770px) {
    .header {
        padding: 0 27px 27px;
    }
}
