.profile__info{
    display: flex;
    gap: 13px;
    max-width: 500px;
    width: 100%;
    align-content: stretch;
    flex-direction: column;
    align-items: flex-start;
}

@media screen and (max-width: 870px) {
    .profile__info {
        gap: 9px 7px;
        align-items: center;
    }
}