.footer__copyright {
    font-family: 'Inter', Arial, Helvetica, sans-serif; /*Альтернативный шрифт, а также семейство шрифтов были добавлены в .page ранее*/
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: #545454;
    margin: 0 auto;
}

@media screen and (max-width:500px) {
    .footer__copyright {
        font-size: 16px;
        line-height: 19px;
    }
}

@media screen and (max-width:320px) {
    .footer__copyright {
        font-size: 14px;
        line-height: 17px;
    }
}